import { useAuthContext } from '../../auth/useAuthContext';
// _mock_
import {
  _appFeatured,
  _appAuthors,
  _appInstalled,
  _appRelated,
  _appInvoices,
} from '../../_mock/arrays';
// components
import CustomBreadcrumbs from '../../components/custom-breadcrumbs/CustomBreadcrumbs';
