// sections
import {
  AppWidget,
  AppWelcomeCard,
  AppWelcome,
  AppFeatured,
  AppNewInvoice,
  AppEventList,
  AppTopAuthors,
  AppTopRelated,
  AppAreaInstalled,
  AppWidgetSummary,
  AppCurrentDownload,
  AppTopInstalledCountries,
  AvatarDate,
} from '../../sections/@dashboard/general/app';
// assets
