import { HOST_API_LOCAL, END_POINT_OBTENER_EMPLEADO } from '../../config-global';
import EmpleadoDialog from './EmpleadoDialog';

// ----------------------------------------------------------------------

export default function GeneralAppPage() {
  const { user } = useAuthContext();

  const theme = useTheme();

  const { themeStretch } = useSettingsContext();
  const name = localStorage.getItem('EmpleadoId');

  const [nombre, setNombre] = useState()
  const [apellidoPaterno, setApellidoPaterno] = useState()

  useEffect(() => {

    function ObtenerEmpleado() {
      const urlEndPoint = HOST_API_LOCAL + END_POINT_OBTENER_EMPLEADO;
      const AUT = `Bearer ${localStorage.getItem('accessToken')}`
      axios({
        method: 'get',
        url: urlEndPoint,
        params: { Id: name },
        headers: { 'Authorization': AUT }
      }).then(response => {
        setNombre(response.data.nombre)
        setApellidoPaterno(response.data.apellidoPaterno)
      }).catch((error) => {
        console.log('algo que podamos identificar', error)
      })
    }
    ObtenerEmpleado()
  }, [name])

  return (
    <>
      <Helmet>
        <title>Inicio</title>
      </Helmet>
      {/* <EmpleadoDialog/> */}
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Grid container spacing={4}>
          <Grid item xs={12} md={6} >
            <Stack mb={3} mt={4}>
              <AppWelcome
                sx={{ padding: 3 }}
                title='¡Bienvenid@ =),'
                subtitle={`${nombre} ${apellidoPaterno}!`}
                description="Si deseas generar una solicitud, ingresa al panel de vacaciones"
                img={
                  <SeoIllustration
                    sx={{
                      p: 3,
                      width: 460,
                      margin: { xs: 'auto', md: 'inherit' },
                    }}
                  />
                }
                action={<Button variant="contained" component={RouterLink} to='/dashboard/vacaciones/inicio'>Solicitar</Button>}
              />
            </Stack>
            {/* Módulo de eventos             */}
            {/* <AppEventList/> */}
          </Grid>
          <Grid item xs={12} md={4}>
            <Stack mb={3} mt={4}>
              <AppWelcomeCard
                sx={{ padding: 3 }}
                subheader="Conoce tu disponibilidad de días"
              />
            </Stack>
          </Grid>

        </Grid>
      </Container>
    </>
  );
}
